import Chart from 'chart.js/auto'
import { getAttendancesByYear } from './api'

(async function() {
  const data = await getAttendancesByYear();

  new Chart(
        document.getElementById('attendances'),
        {
          type: 'bar',
          options: {
            animation: false,
            plugins: {
              legend: {
                display: false
              },
              tooltip: {
                enabled: false
              }
            }
          },
          data: {
            labels: data.map(row => row.year),
            datasets: [
              {
                label: 'Attendances by year',
                data: data.map(row => row.count)
              }
            ]
          }
        }
    );
})();
